import axios from 'axios'
import { FAIL, START, SUCCESS } from '../constants/common'

export const getMoviesUrl = () => process.env.REACT_APP_POPULAR_MOVIES
export const getSearchUrl = () => process.env.REACT_APP_SEARCH_MOVIE

export async function get(apiNamespace) {
  const { REACT_APP_BASE_URL, REACT_APP_KEY } = process.env
  const urlApi = `${REACT_APP_BASE_URL}${apiNamespace}&&api_key=${REACT_APP_KEY}`
  try {
    const { data } = await axios.get(urlApi)
    return data
  } catch (err) {
    console.info(err)
    return new Error({ success: false, message: err })
  }
}

export function reduceSuccessFail({
  reducer,
  state,
  action,
  onSuccess,
  isObservable = true,
}) {
  const { stage, error } = action
  if (reducer === action.reducer && isObservable) {
    switch (stage) {
      case START:
        state = state.merge({ loading: true, error: null })
        break
      case FAIL:
        state = state.merge({ loading: false, error })
        break
      case SUCCESS:
        state = state.merge({ loading: false, loaded: true, error: null })
        break
      default:
    }
  }

  switch (stage) {
    case SUCCESS:
      return onSuccess(state)
    default:
      return state
  }
}

/**
 * –û–±'—î–∫—Ç –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ–≤ –¥–ª—è —Ñ—É–Ω–∫—Ü—ñ—ó actionSuccessFail
 * @typedef {Object} ActionParams
 * @property {function} dispatch - —Ñ—É–Ω–∫—Ü—ñ—è dispatch, —è–∫—É –ø–µ—Ä–µ–¥–∞—î redux-thunk
 * @property {string} reducer - –Ω–∞–∑–≤–∞ —Ä–µ–¥—é—Å–µ—Ä–∞, –¥–ª—è —è–∫–æ–≥–æ –ø—Ä–∏–∑–Ω–∞—á–µ–Ω–∏–π —Ü–µ–π –µ–∫—à–Ω
 * @property {string} type - —Ç–∏–ø –µ–∫—à–∏–Ω–∞
 * @property {Promise} promise - –ø—Ä–æ–º—ñ—Å (–∞–±–æ –∞—Å–∏–Ω—Ö—Ä–æ–Ω–Ω–∞ —Ñ—É–Ω–∫—Ü—ñ—è), —è–∫–∏–π –ø–æ—Ç—Ä—ñ–±–Ω–æ –≤–∏–∫–æ–Ω–∞—Ç–∏
 * @property {Object} [payload] - –ø–µ–π–ª–æ–∞–¥ –µ–∫—à–∏–Ω–∞, –±—É–¥–µ –¥–æ—Å—Ç—É–ø–Ω–∏–π –¥–ª—è —Ä–µ–¥—é—Å–µ—Ä–∞
 */

export const actionSuccessFail = ({
  dispatch,
  reducer,
  type,
  promise,
  payload,
}) => {
  dispatch({ reducer, type: type + START, stage: START })
  return promise
    .then(response => {
      if (response && response.success === false) {
        return dispatch({
          reducer,
          type: type + FAIL,
          stage: FAIL,
          error: (response.errors || []).join('\r\n'),
        })
      } else {
        return dispatch({
          reducer,
          type: type + SUCCESS,
          stage: SUCCESS,
          response,
          payload,
        })
      }
    })
    .catch(error => {
      console.error(error)
      dispatch({
        reducer,
        type: type + FAIL,
        stage: FAIL,
        error: error.toString(),
      })
    })
}
